<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Корзина </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"
        >Количество товаров: {{ $store.state.cartProducts.length }}</span
      >
    </div>
    <div class="load" v-if="$store.state.cartLoading">
      <hr />
      <hr />
      <hr />
      <hr />
    </div>
    <section class="cart" v-if="$store.state.cartLoadingFailed">
      Произошла ошибка...<button class="button" @click.prevent="loadCart">
        &#8635; Обновить
      </button>
    </section>
    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem
              v-for="item in products"
              :key="item.productId"
              :item="item"
            />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link tag="button" class="cart__button button button--primery" type="submit" :to="{name: 'order'}">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
import { mapGetters, mapActions } from "vuex";
import CartItem from "@/components/CartItem";


export default {
  filters: {
    numberFormat,
  },
  components: { CartItem },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
  methods: {
    ...mapActions(["loadCart"]),
  },

  
};
</script>


