<template>
<main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        Количество товаров: {{ $store.state.cartProducts.length }}
      </span>
    </div>

    <section class="cart">
      <div class="cart" v-if="$store.state.cartLoadingFailed">Произошла ошибка при загрузке...<button class="button" @click.prevent="loadCart">&#8635; Обновить</button></div>

      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">

						<BaseFormText title="ФИО" :error="formError.name" v-model="formData.name" placeholder="Введите ваше полное имя"/>

						<BaseFormText title="Адрес доставки" :error="formError.address" v-model="formData.address" placeholder="Введите ваш адрес"/>

						<BaseFormText title="Телефон" :error="formError.phone" type="tel" v-model="formData.phone" placeholder="Введите ваш телефон"/>

						<BaseFormText title="Email" :error="formError.email" type="email" v-model="formData.email" placeholder="Введи ваш Email"/>
						
						<BaseFormTextArea title="Комментарий к заказу" :error="formError.comment" v-model="formData.comment" placeholder="Ваши пожелания"/>

						</div>

          <div class="cart__options">
            <div class="cart load" v-if="$store.state.cartLoading"><hr /><hr /><hr /><hr /></div>
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders" >
            <li class="cart__order" v-for="item in products" :key="item.productId">
              <h3> {{ item.product.title }}</h3>
              <b>{{ (item.amount * item.product.price) | numberFormat }} ₽</b>
              <span>Артикул: {{ item.productId }} </span>
            </li>
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>Самовывоз </b></p>
            <p>Итого: <b>{{ $store.state.cartProducts.length }}</b> товара на сумму <b> {{ totalPrice | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" type="submit" :disabled="formSend">
            Оформить заказ
          </button>
            <div v-show="formSending">Заявка отправляется...</div>
            <div v-show="formSend">Ваша заявка успешно отправлен!</div>

        </div>
        <div class="cart__error form__error-block" v-if="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextArea from '@/components/BaseFormTextArea';
import numberFormat from '@/helpers/numberFormat';
import { mapGetters, mapActions} from 'vuex';
import axios from "axios";
import { API_BASE_URL } from "@/config";


export default {
  filters: {numberFormat},

	components: { BaseFormText, BaseFormTextArea },

	data() {
		return{
			formData: {},
			formError: {},

      formErrorMessage: '',

      formSend: false,
      formSending: false,
		}
	},
	computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
    }),
  },
  methods: {
    ...mapActions(['loadCart']),
    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formSend = false;
      this.formSending = true;

      axios
      .post(API_BASE_URL + '/api/orders', {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      })
      .then((response) => {
        this.$store.commit('resetCart');
        this.$store.commit('updateOrderInfo', response.data);
        this.formData = {};
        this.formSend = true;
        this.formSending = false;
        this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
      })
      .catch(error => {
        this.formError = error.response.data.error.request || {};
        this.formErrorMessage = error.response.data.error.message;
        this.formSending = false;
      })
    },
  },
}
</script>